.mainLogo {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: contain;
  opacity: 0.3;
}

.RandomPhrase {
  display: flex;
  flex-direction: column;
  color: green;
  justify-content: center;
  align-items: center;
}

.score {
  margin-bottom: 24px;
}

.classic {
  margin-top: 24px;
  color: red;
}

span, strong {
  font-size: 30px;
}

strong {
  color: green;
}


.inputWrapper {
  margin-top: 8px;
  height: 24px;
}

input {
  height: 24px;
  width: 220px;
  color: green;
  background-color: transparent;
}


.heron, .heronLogo {
  width: 250px;
  height: 250px;
  opacity: 0.8;
}

.heron {
  position: absolute;
  bottom: 10%;
  position: absolute;
  animation: linear infinite alternate;
  animation-name: run;
  animation-duration: 3s;
}

@keyframes run {
  0% { left: 0; bottom: 20%;}
  100% { left: 80%; bottom: 50%; }
}